<template>
    <div style="margin:30px">
        <el-divider></el-divider>
        <el-descriptions class="margin-top" title="实验基础信息" :column="3" :size="size">
            <template slot="extra">
                <router-link :to="{ name: '活动管理', params: {campaign_id: this.$route.params.campaign_id}}" style='text-decoration: none;color: aliceblue;' align="right">
                    <el-button class="" ><i class="el-icon-arrow-left" style="margin-right: 10px;"></i>返回活动管理</el-button>
                </router-link>
            </template>
            <el-descriptions-item label="实验名称">{{abMetaData.name}}</el-descriptions-item>
            <el-descriptions-item label="实验类型">
                <el-tag size="small" type="primary">{{abMetaData.app_type}}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="创建于">
                <timeago :datetime="abMetaData.creation_date" locale="zh-CN"></timeago>
            </el-descriptions-item>
            <el-descriptions-item label="实验描述">{{abMetaData.description}}</el-descriptions-item>

        </el-descriptions>
        <el-collapse v-model="activeNames">
            <el-collapse-item title="实验组创建及策略配置" name="1">
              <el-col :span="24" class="toolbar" style="padding-bottom: 10px;">
                  <el-button class="return-list1" type="primary" style="margin-left:15px;margin-right: 10px" @click.native="addNewExp"><i class="el-icon-plus" style="margin-right: 5px;margin-left:0px"></i>保存</el-button>
                  <router-link :to="{ name: '活动管理', params: {campaign_id: this.$route.params.campaign_id}}" style='text-decoration: none;color: aliceblue;' align="right">
                  <el-button class="return-list1" >取消</el-button>
                 </router-link>
              </el-col>
                <el-col :span="24">
                    <el-form :model="form" ref="form" :rules="FormRules">
                        <div style="border: 1px solid #e6e6e6;margin-bottom: 10px;padding:15px">
                            <p style="margin-bottom: 0;">测试您的产品的多种变化。</p>
                            <p> 注意：确保你的曝光率加起来是100%，并且你选择了一个基本的变体（出错的时候会统统发送给它）。</p>
                            <br>
                            <el-row :gutter="10">
                                <el-col :span='22'>
                                    <el-table :data="form.modelData" highlight-current-row ref="multipleHeadTable"
                                              @selection-change="selsExp">
                                        <el-table-column label-class-name="出错时将只向选择实例发送请求"
                                                         :render-header="openAnnotation"
                                                         type="selection"
                                                         min-width="5%" label="默认">
                                        </el-table-column>
                                        <el-table-column  prop="altName"
                                                         label="实验组"
                                                         min-width="20%"
                                                         label-class-name="曝光的实验实例名称"
                                                         :render-header="openAnnotation">
                                            <template slot-scope="scope">
                                                <el-input v-model.trim="scope.row.altName" :value="scope.row.altName" placeholder="请输入实验组名称"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column  prop="altName"
                                                         label="实验组描述"
                                                         min-width="30%"
                                                         label-class-name="实验组描述"
                                                         :render-header="openAnnotation">
                                            <template slot-scope="scope">
                                                <el-input v-model.trim="scope.row.description" :value="scope.row.description" placeholder="请输入实验组描述"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="exposureNum"
                                                         label="流量分配(%)" min-width="10%"
                                                         label-class-name="各实例曝光比例"
                                                         :render-header="openAnnotation">
                                            <template slot-scope="scope">
                                                <el-input v-model="scope.row.exposureNum" placeholder="请输入流量比率"></el-input>
                                            </template>
                                        </el-table-column>
                            <el-table-column  prop="modelname"
                                             label="策略配置" min-width="20%"
                                             label-class-name="可以从策略库挑选或单独创建"
                                             :render-header="openAnnotation"
                                             >
                                <template slot-scope="scope">
                                    <el-input v-model.trim="scope.row.modelname" :value="scope.row.modelname" placeholder="请输入策略"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="" min-width="10%">
                                <template slot-scope="scope">
                                    <i class="el-icon-delete" style="font-size:30px;cursor:pointer;" @click="delModelHead(scope.$index)"></i>
                                </template>
                            </el-table-column>
                            <el-table-column label="" min-width="15%" style="margin-right:6px">
                                <template slot-scope="scope">
                                    <el-button v-if="scope.$index===(form.modelData.length-1)" size="mini" class="el-icon-plus" @click="addModelHead"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
                <br>
               <el-row :gutter="10"  v-show="abMetaData.app_type!='普通实验'">
                   <h4 style="padding-left: 8px">请选择策略库的策略</h4>
                <br>
                <el-col :span="6">
                    <div style="height:400px;line-height:100px;overflow:auto;overflow-x:hidden;border: 1px solid #e6e6e6">
                        <el-menu class="el-menu-vertical-demo"
                                 active-text-color="rgb(32, 160, 255)"
                                 :unique-opened="true"
                                 @select="handleSelect">
                            <el-menu-item index="-1" @click.native="getModelList()"><i class="el-icon-menu"></i>所有策略</el-menu-item>
                            <template v-for="(item,index) in ModelProjects">
                                <el-menu-item :index="index+''" :key="item.id" @click.native="getModelList(item.id)" class="group">
                                    <template slot="title">{{item.name}}
                                    </template>
                                </el-menu-item>
                            </template>
                        </el-menu>
                    </div>
                </el-col>
                <el-col :span="18">
                    <el-table :data="searchApiList" highlight-current-row v-loading="apiListLoading"
                              style="width: 100%;" :show-header="true" max-height="400" @selection-change="selsChange">
                        <el-table-column type="selection" width="55">
                        </el-table-column>
                        <el-table-column prop="modelname" label="策略名称" min-width="15%" sortable>
                            <template slot-scope="scope">
                                {{scope.row.model.name}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="description" label="策略描述" min-width="25%" sortable>
                            <template slot-scope="scope">
                                {{scope.row.model.description}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="status" label="策略状态" min-width="25%" sortable>
                            <template slot-scope="scope">
                                <!--<img v-show="scope.row.status" src="../../../assets/icon-yes.svg"/>
                                <img v-show="!scope.row.status" src="../../../assets/icon-no.svg"/>-->
                                <span v-show="instance_status==='ok'&&instance_id===scope.row.id"
                                      style="padding: 2px 4px;color: #fff;background-color: #4BB74B;">已上线</span>
                                <span v-show="instance_status==='failed'&&instance_id===scope.row.id"
                                      style="padding: 2px 4px;color: #fff;background-color: #C0C0C0;">未上线</span>
                                <el-button @click="handleModelTest(scope.row)" type="text" style="margin-left:5px" size="medium">测试</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row  v-show="abMetaData.app_type!='普通实验'">
            <el-col :span="24" class="toolbar">
                <el-pagination layout="prev, pager, next" @current-change="handleCurrentChangeApi" :page-size="10" :page-count="total" style="float:right;">
                </el-pagination>
            </el-col>
                        </el-row>
                    </div>
                </el-form> </el-col>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>
import { test } from '@/api/api'
import $ from 'jquery'

export default {
  data () {
    return {
        baseExpSelected: [],
        mlexp:false,
        gexp:false,
        abMetaData:{},
        activeNames:["1"],
        ModelProjects: [],
        instance_status:"failed",
        instance_id:0,
        apiListLoading: false,
        activeIndex: '',
        searchApiList: [],
        page: 1,
        total: 0,
        sels: [], // 列表选中列
        form: {
            abname: '',
            description: '',
            modelData: [{ altName: '', description:'',modelname: 'No Strategy', exposureNum: 50 }]
        },
        FormRules: {
            modelname: [{ required: true, message: '请输入实验用例名称', trigger: 'blur' }],
            altlist: [{ required: true, message: '请输入待测试的模型列表', trigger: 'blur' }]
        }
    }
  },
  methods: {
      getAbMetaData () {
          this.listLoading = true
          let self = this
          $.ajax({
              type: 'get',
              url: test + '/api/campaigns/get_abexp_metadata',
              async: true,
              data: { ab_id: this.$route.params.ab_id},
              headers: {
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: function (data) {
                  self.listLoading = false
                  if (data.code === '999999') {
                      self.abMetaData = data.data.data
                  } else {
                      self.$message.error({
                          message: data.msg,
                          center: true
                      })
                  }
              }
          })
      },
    addModelHead () {
      let headers = { modelname: 'No Strategy',description:'', exposureNum: 50, altName: 'alt_name' }
      this.form.modelData.push(headers)
      // let rows = [this.form.modelData[this.form.modelData.length - 1]]
      // this.form.modelData[this.form.modelData.length-1].exposureNum=50
      // this.toggleHeadSelection(rows)
    },
    delModelHead (index) {
      if (this.form.modelData.length !== 1) {
        this.form.modelData.splice(index, 1)
      }
    },
    handleSelect (key, keyPath) {
      this.activeIndex = key
      console.log(this.activeIndex)
    },

    handleCurrentChangeApi (val) {
      // console.log(this.groupData[this.activeIndex]["id"]);
      if (this.activeIndex['id']) {
        this.page = val
        this.getModelList()
      } else {
        this.page = val
        this.getModelList()
      }
    },
      
      selsExp (sels) {
      this.baseExpSelected = sels
    },

    // 获取api分组
    getModelsGroup () {
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/models/project_group',
        async: true,
        data: { ab_id: this.$route.params.ab_id },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.ModelProjects = data.data.data
            console.log(self.ModelProjects,"self.ModelProjects")
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleModelTest(instance){
      this.apiListLoading = true
      let self = this
      let param = JSON.stringify({ ab_id: Number(this.$route.params.ab_id), 
                   ins_id: instance.id })
    console.log(param,"ins_is")
      $.ajax({
        type: 'post',
        url: test + '/api/models/api_status_test',
        async: true,
        data: param,
        headers: {
            'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.apiListLoading = false
          if (data.code === '999999') {
            self.instance_status = data.data.data.status
            self.instance_id = data.data.data.instance_id
              console.log(self.instance_status,"self.instance_status")
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })        
    },
    getModelList (_list) {
      this.apiListLoading = true
      let self = this
      let param = {ab_id: Number(this.$route.params.ab_id), 
                   page: self.page }
      if (_list) {
        param['model_project_id'] = Number(_list)
      }
      $.ajax({
        type: 'get',
        url: test + '/api/models/api_list',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.apiListLoading = false
          if (data.code === '999999') {
            self.searchApiList = data.data.data
            self.total = data.data.total
            console.log(self.searchApiList, 'searchApiList')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },

    handleCurrentChange (val) {
      this.currentRow = val
    },
    selsChange (sels) {
      this.sels = sels
      if (sels.length === 1 && this.form.modelData[0].altName === '') {
        // 删除第一个空记录
        this.form.modelData.splice(0, 1)
      }
        // 从模型api信息填充
      sels.forEach((item) => {
          let _item = item
        _item.exposureNum = 50
        _item.altName = item.name
        _item.modelname=item.model.name
          console.log(_item,"ddddddddd")
        this.form.modelData.push(_item)
        this.form.modelData = Array.from(new Set(this.form.modelData))
      })

    },
    addNewExp () {
      let self = this
      this.$refs.form.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交实验吗？', '提示', {}).then(() => {
            let param = JSON.stringify({
              ab_id: Number(self.$route.params.ab_id),
              expRegData: self.form.modelData,
              // baseExpSelected: self.baseExpSelected,
              //description: self.form.description// "A/B exp create"
            })
            console.log(param,"param")
            $.ajax({
              type: 'post',
              url: test + '/api/campaigns/add_exp_alt',
              async: true,
              data: param,
              headers: {
                'Content-Type': 'application/json',
                Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: function (data) {
                if (data.code === '999999') {
                  self.$router.push({ name: '实验组信息',
                    params: { ab_id: self.$route.params.ab_id,
                             campaign_id: self.$route.params.campaign_id}})
                  self.$message({
                    message: '保存成功',
                    center: true,
                    type: 'success'
                  })
                } else {
                  self.$message.error({
                    message: data.msg,
                    center: true
                  })
                }
              }
            })
          }).catch(() => {
          })
        }
      })
    },

    // addApi: function () {
    // header 需要"Content-Type": "application/json"
    handleChange (val) {
    },
    openAnnotation: function (h, { column, $index }) {
      return (
        <el-tooltip class="item" effect="dark" content={column.labelClassName} placement="top">
          <span>{column.label}</span>
        </el-tooltip>
      )
    }
  },
  watch: {
    form: {
      deep: true
    }
  },
  computed: {
  },
  mounted () {
    this.getAbMetaData()
    this.getModelsGroup()
    this.getModelList()
  }
}
</script>

